import Vue from 'vue'
import Router from 'vue-router'
import Sign from '@/components/parent/Sign.vue'
import Delivery from '@/components/parent/Delivery.vue'
import Shop from '@/components/parent/Shop.vue'
import User from '@/components/parent/User.vue'

import SignIn from '@/components/child/SignIn.vue'
import SignUp from '@/components/child/SignUp.vue'
import axios from 'axios'

Vue.use(Router)

// eslint-disable-next-line no-unused-vars
export const router = new Router({
  routes: [
    {
      path: '/sign',
      name: 'Sign',
      component: Sign,
      children: [
        {
          path: '/sign/in',
          name: 'SignIn',
          component: SignIn
        },
        {
          path: '/sign/up',
          name: 'SignUp',
          component: SignUp,
          meta: {
            requireAuth: true,
          }
        }
      ]
    },
    {
      path: '/delivery',
      name: 'Delivery',
      component: Delivery
    },
    {
      path: '/shop',
      name: 'Shop',
      component: Shop
    },
    {
      path: '/user',
      name: 'User',
      component: User
    }
  ]
})
router.beforeEach((to, from, next) => {
  if (to.meta.requireAuth) {  // 判断该路由是否需要登录权限
    axios({
        method: 'get',
        url: '/confirm'
      }).then(function (res) {
      let b = res.data

      if (b == true) {
        next()
      } else {
        next({
          path: '/sign/in',
          query: {redirect: to.fullPath}  // 将跳转的路由path作为参数，登录成功后跳转到该路由
        })
      }
    })
      .catch(function (err) {
        console.log(err)
      })
  } else {
    next()
  }
})



