<template>
  <div>
  <h1>SignIn.vue</h1>
    <button @click="signin">登录</button>
    <button @click="signout">退出登录</button>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  methods:{
    signin(){
      axios.post('/signin/user').then(function () {
        alert(this.$route.query)
      })
    },
    signout(){
      axios.delete('/signout/user')
    }
  }
}
</script>

<style>

</style>



